body.dark-theme {
  background: linear-gradient(90deg, rgb(22, 20, 20) 20%, rgb(68, 46, 46) 50%, rgb(22, 20, 20) 80%);
  color: $additionalColor;
  p{
    color: $additionalColor;
  }
  .modal_window{
    background: linear-gradient( 90deg, rgb(36, 33, 33) 20%, rgb(65, 45, 45) 50%, rgb(88, 64, 64) 80%)
  }.modal_btn{
    color: rgb(183, 191, 194);
    border: 2px solid rgb(94, 107, 59);
    &:hover,
    &:focus{
      color: $additionalColor;
      border: none;
    }
  }
  .tui-page-btn {
    color: $additionalColor;
  }
  .container {
    color: $additionalColor;
  }
  .tui-first-child{
    color: $additionalColor;
  }
  .tui-next{
    color: $additionalColor;
  }
  .tui-prev-is-ellip{
    color: $additionalColor; 
  }
  .tui-last-child{
    color: $additionalColor;
  }
}
p.dark-theme {
  background: linear-gradient(90deg, rgb(0, 0, 0) 10%, rgb(47, 46, 46) 50%, rgb(0, 0, 0) 90%);
  color: $additionalColor;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: absolute;
  width: 64px;
  height: 32px;
  border-radius: 50em;
  padding: 3px 0;
  right: 10%;
  top: 180px;
  z-index: 1;
  // color: white;
}
.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}
.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: rgb(97, 94, 94);
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
}
.theme-switch__marker {
    position: relative;
    background-color: #fff;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
    transform: translateX(3px);
    transition: transform 250ms var(--timing-function);
  }
.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #d3d3d4;
  border-color: $logoIconColor
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(35px);
}
.theme-switch__toggle:focus + .theme-switch__track,
.theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 2px #ff6b08;
}